
import React, {useEffect} from "react"
import { Jumbotron, Container, Button } from "reactstrap"


const Home = () => {

    useEffect(() => {
        document.title = "Home";
    }, []);

    return(
        <div>
            <Jumbotron className = "text-center">
                <h1>Code with Sid</h1>
                <p>
                    This page displays the courses I took during my Master's in Computer Science from University College Dublin
                </p>
                <Container>
                    <Button color = "info">Start Using</Button>
                </Container>
            </Jumbotron>          
        </div>    
    )
}

export default Home;